import { bindings, hook } from '@vl/redata';
import CardWalletModel from '@uz/unitz-models/CardWalletModel';
import useObservableSource from '@vl/hooks/useObservableSource';

const bindData = bindings({
  pageContainer: {
    rules: [
      [
        'data',
        {
          data: {
            cardList: hook((ctx) => {
              const userId = ctx.apply('authModel.getUserId');
              // const userId = '9KGhxtVGdPMD4VRIuZw25Iwo8x33';
              const items = useObservableSource(
                () => CardWalletModel.find(
                  `limit: 20, where:{user_id: {_eq: "${userId}"}}, order_by: {created_at: asc}`,
                  'id card_type card_number is_default bank_code'
                ),
                [userId]
              );
              return items;
            }),
          },
        },
      ],
    ],
  },

  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
