import React from 'react';
import DIV from '@vl/redata/DIV.macro';
import { UnAuthRedirect } from '@uz/unitz-components-web/AppRedirect';
import { ctx } from '@vl/redata';
import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';
import DesktopView from './components/DesktopView';
import MobileView from './components/MobileView';

const Index = () => {
  return (
    <DIV className="profileMenuModel">
      <UnAuthRedirect to={ctx.apply('authModel.generateLoginRedirectUrl')}>
        <ResponsiveProps
          // md={{ renderer: RenderNavbarMediumSize }}
          xs={{ renderer: () => <MobileView /> }}
          lg={{ renderer: () => <DesktopView /> }}
        >
          {(resProps) => !!resProps && resProps.renderer()}
        </ResponsiveProps>
      </UnAuthRedirect>
    </DIV>
  );
};

export default Index;
