import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import gstyles from '@vl/gstyles';
import { Anchor } from 'antd';
import styled from 'styled-components';
import _ from 'lodash';
import cx from 'classnames';

const { Link } = Anchor;
const CustomAnchor = styled.div`
  .ant-anchor-wrapper {
    margin: 0;
    padding: 0;
  }
  .ant-anchor-ink {
    display: none;
  }
  .ant-anchor-link {
    padding: 0;
  }
`;
const View8 = () => {
  return (
    <DIV className="userMenus">
      <section className="flex flex-col py-0">
        <CustomAnchor>
          <Anchor getContainer={() => window} onClick={ctx.get('drawerProps.showDrawer')} affix={false}>
            {ctx.get('userMenus.data').map(
              (menu) => !!menu.show && (
                <Link
                  className={cx('p-0 bg-white500', {
                    ' mt-3': menu.id === 'menuHelpCenter',
                  })}
                  key={menu.id}
                  href={`${menu.href}`}
                  title={
                    <div
                      id={menu.id}
                      className="flex items-center justify-between text-base text-main font-normal cursor-pointer py-4 px-3 rounded-lg"
                      onClick={() => ctx.apply('userMenus.onSelectMenu', menu)}
                    >
                      <div className="flex items-center">
                        {gstyles.icons({
                          className: 'mr-3',
                          name: menu.icon,
                          size: _.get(menu, 'iconSize', 24),
                          fill: gstyles.colors.sub,
                        })}
                        <span className="text-ellipsis-4">{menu.text}</span>
                      </div>
                      <div>
                        {menu.id === 'menuNotification' ? (
                          <div className="bg-red500 rounded-md font-semibold mr-2 text-xs py-1 text-white500 px-2">
                            {ctx.get('notifications.total')}
                          </div>
                        ) : (
                          gstyles.icons({
                            className: 'mr-2',
                            name: 'arrow-right',
                            size: 24,
                            fill: gstyles.colors.sub,
                          })
                        )}
                      </div>
                    </div>
                  }
                />
              )
            )}
          </Anchor>
        </CustomAnchor>
      </section>
    </DIV>
  );
};

export default View8;
