import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';
import useRoute from '@vl/hooks/useGbRoute';

const bindData = bindings({
  userMenus: {
    rules: [
      [
        'data',
        {
          data: {
            userMenus: hook((ctx) => {
              const route = useRoute();
              const hash = _.get(route, 'location.hash', '');
              const [activeMenu, $activeMenu] = React.useState(hash);
              const menuData = [
                {
                  href: '#info',
                  id: 'menuIntroduce',
                  text: ctx.apply('i18n.t', 'ProfileUser.infoProfile'),
                  icon: 'account',
                  show: true,
                },
                {
                  href: '#notification',
                  id: 'menuCategory',
                  text: ctx.apply('i18n.t', 'Profile.notification'),
                  icon: 'notification',
                  show: true,
                },
                {
                  href: '#wallet',
                  id: 'menuPhoto',
                  text: ctx.apply('i18n.t', 'Profile.myWallet'),
                  icon: 'account_balance_wallet',
                  show: true,
                },
                {
                  href: '#bank',
                  id: 'menuVideo',
                  text: ctx.apply('i18n.t', 'ProfileUser.listCard'),
                  icon: 'bank-cards',
                  show: true,
                },
                {
                  href: '#transaction',
                  id: 'menuStep',
                  text: ctx.apply('i18n.t', 'Profile.transactionHistory'),
                  icon: 'receipt',
                  show: true,
                },
                {
                  href: '#history',
                  id: 'menuStyle',
                  text: ctx.apply('i18n.t', 'ProfileUser.callHistory'),
                  icon: 'history',
                  show: true,
                },
                {
                  href: '#help-center',
                  id: 'menuTime',
                  text: ctx.apply('i18n.t', 'Profile.helpCenter'),
                  icon: 'help',
                  show: true,
                },
                {
                  href: '#setting',
                  id: 'menuPackage',
                  text: ctx.apply('i18n.t', 'ProfileUser.setting'),
                  icon: 'setting',
                  show: true,
                },
              ];
              const triggerClickMenu = (menuId) => {
                if (!menuId) return;
                const element = document.querySelector(menuId);
                if (element) {
                  element.click();
                }
              };
              React.useEffect(() => {
                if (hash) {
                  const menu = hash.replace('section', 'menu');
                  triggerClickMenu(menu);
                }
              }, []);
              return {
                data: menuData,
                activeMenu,
                onSelectMenu: (item) => {
                  const id = _.get(item, 'id', 'sectionIntroduce');
                  // ctx.apply('@refs.scrollToRef', ctx.get(`@refs.${id}`));
                  $activeMenu(id);
                },
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
