import React from 'react';
import { bindings, hook } from '@vl/redata';

import _ from 'lodash';
import useRoute from '@vl/hooks/useGbRoute';
import UserWalletModel from '@uz/unitz-models/UserWalletModel';
import UserModelFormatter from '@uz/unitz-models/UserModel/formatter';
import useObservableSource from '@vl/hooks/useObservableSource';

import UserModel from '@uz/unitz-models/UserModel';
import usePromiseSource from '@vl/hooks/usePromiseSource';

const STEP_LIST = ['info', 'notification', 'wallet', 'bank', 'transaction', 'history', 'help-center', 'setting'];

const STEP_LIST_BY_NAME = {};
_.map(STEP_LIST, (val, index) => (STEP_LIST_BY_NAME[val] = index));

const bindData = bindings({
  ProfileMenuDesktop: {
    rules: [
      [
        'data',
        {
          data: {
            wallets: hook((ctx) => {
              const userId = ctx.apply('authModel.getUserId');
              const wallets = useObservableSource(() => UserWalletModel.find(
                `where: {user_id: {_eq: "${userId}"}}`,
                `wallets {
                  balance {
                    amount_available
                  }
                }
                id`
              ));
              return wallets;
            }),
            userInfo: hook((ctx) => {
              const userId = ctx.apply('authModel.getUserId');

              const items = usePromiseSource(
                async () => {
                  if (!userId) return null;
                  try {
                    const userProfile = await UserModel.find(
                      `where: {id: {_eq: "${userId}"}}`,
                      `profile {
                        avatar_url
                        display_name
                        id
                        phone_number
                      }`
                    );

                    return userProfile.toObject();
                  } catch (err) {}
                },
                null,
                [userId]
              );

              const userInfoData = _.get(items, '0');
              return {
                displayName: UserModelFormatter.displayName(ctx)(userInfoData),
                avatarUrl: UserModelFormatter.avatarUrl(ctx)(userInfoData),
                phoneNumber: UserModelFormatter.profilePhoneNumber(ctx)(userInfoData),
              };
            }),
            stepModel: hook((ctx) => {
              const route = useRoute();
              const hashName = _.get(route, 'location.hash', '').split('#')[1];

              const [currentStep, $currentStep] = React.useState(_.first(STEP_LIST));
              React.useEffect(() => {
                if (currentStep !== hashName) {
                  $currentStep(hashName);
                }
              }, [currentStep, $currentStep, hashName]);

              return {
                currentStep,
                setStep: (newStep) => _.has(STEP_LIST_BY_NAME, newStep) && $currentStep(newStep),
                hasNext: () => {},
                nextStep: () => {},
                prevStep: () => {},
              };
            }),
          },
        },
      ],
    ],
  },
  infoStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[0]),
        },
      ],
    ],
  },
  notificationStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[1]),
        },
      ],
    ],
  },
  walletStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[2]),
        },
      ],
    ],
  },

  bankStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[3]),
        },
      ],
    ],
  },
  transactionStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[4]),
        },
      ],
    ],
  },

  historyStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[5]),
        },
      ],
    ],
  },
  helpCenterStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[6]),
        },
      ],
    ],
  },
  settingStep: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.get('stepModel.currentStep') === STEP_LIST[7]),
        },
      ],
    ],
  },

  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
