import React from 'react';
import { bindings, hook } from '@vl/redata';
import AdvisorProfileFormatter from '@uz/unitz-models/AdvisorProfileModel/formatter';
import TransactionFormatter from '@uz/unitz-models/TransactionModel/formatter';
import _ from 'lodash';
import UserModel from '@uz/unitz-models/UserModel';
import PagingModel from '@uz/unitz-models/PagingModel';
import { useFormik } from 'formik';
import * as Yup from 'yup';

const bindData = bindings({
  pageContainer: {
    rules: [
      [
        'data',
        {
          data: {
            // categoryData: hook((ctx) => {
            //   const items = useObservableSource(() => CategoryModel.find('limit: 20', 'id avatar_url display_name'));
            //   return items;
            // }),
            '@page': hook((ctx) => {
              const [showDetail, $showDetail] = React.useState(false);
              const [transactionDetail, $transactionDetail] = React.useState({});
              return {
                showDetail,
                $showDetail,
                transactionDetail,
                $transactionDetail,
                onClickRow: (record) => {
                  $transactionDetail(record);
                  $showDetail(true);
                },
              };
            }),
            '@form': hook((ctx) => {
              const form = useFormik({
                initialValues: {
                  startDate: '',
                  endDate: '',
                  status: '',
                },
                onSubmit: async (values, actions) => {
                  // TODO Submit Transaction.
                  try {
                    ctx.apply('@pagination.onLoadPage', 0);
                    // const transactionPagination = ctx.apply('REF.getRef', 'transactionPagination');
                    // if (transactionPagination) {
                    //   await transactionPagination.onLoadPage(0);
                    // }
                  } catch (err) {
                    console.log({ err });
                  }
                },
                validationSchema: Yup.object().shape({
                  startDate: Yup.string(),
                  endDate: Yup.string(),
                  status: Yup.string(),
                }),
              });
              return form;
            }),
            triggerSubmit: hook((ctx) => {
              React.useEffect(() => {
                ctx.apply('@form.handleSubmit');
              }, [ctx.get('@form.values.startDate'), ctx.get('@form.values.endDate'), ctx.get('@form.values.status')]);
            }),
            '@pagination': hook((ctx) => {
              const ref = React.useRef({});
              _.assign(ref.current, { ctx });
              const _userId = ctx.apply('authModel.getUserId');
              const dataLoader = React.useCallback(async (options) => {
                // query d liu paging review.
                const values = ref.current.ctx.get('@form.values');
                const { startDate, endDate, status } = values;

                const userId = _userId;
                try {
                  const PAGE_SIZE = 10;
                  const page = _.get(options, 'page', 0);
                  const pageCount = _.get(options, 'pageCount', PAGE_SIZE);
                  const condition = [];
                  if (startDate && endDate) {
                    condition.push(`created_at: {_gte: "${startDate}", _lte: "${endDate}"}`);
                  }
                  if (status) {
                    condition.push(`session_detail: {status: {
                      ${status === 'unknown' ? '_is_null: true' : ''}
                      ${status !== 'unknown' ? `_eq: "${status}"` : ''}
                   }}`);
                  }

                  const limit = `limit: ${pageCount}`;
                  const offset = `offset: ${pageCount * page}`;
                  const orderBy = 'order_by: {created_at: desc}';
                  const queryField = `
                  purchases(where: {${condition.join(',')}}, ${[limit, offset, orderBy].join(', ')}) {
                    id
                    created_at
                    discounts {
                      amount
                      percentage
                      type
                      purchase_id
                    }
                    status(order_by: {created_at: desc}) {
                      id
                      status
                      is_active
                    }
                    payments {
                      id
                      amount
                      tx_id
                      tx_ref
                      tx_status
                      type
                      created_at
                    }
                    services {
                      id
                      per_amount
                      per_unit
                      price_amount
                      price_currency
                      service_id
                      advisor_service {
                        advisor {
                          id
                          profile {
                            id
                            avatar_url
                            display_name
                          }
                        }
                        service{
                          id
                          name
                          kind
                        }
                      }
                    }
                  }
                  purchases_aggregate(where: {${condition.join(',')}}){
                      aggregate {
                        count
                      }
                    }
                `;
                  const res = await UserModel.find(`where: {id: {_eq: "${userId}"}}`, queryField);
                  const data = res.toObject();
                  const purchases = _.get(data, '0.purchases');
                  const dataSource = _.map(purchases, (purchase) => {
                    const services = _.get(purchase, 'services.0');
                    const advisorService = _.get(services, 'advisor_service');
                    const profile = _.get(advisorService, 'advisor.profile');
                    const service = _.get(advisorService, 'service');
                    const status = _.get(purchase, 'status.0');
                    const payments = _.get(purchase, 'payments.0');

                    return {
                      key: TransactionFormatter.id(ctx)(purchase),
                      createdAt: TransactionFormatter.createdAtWithTime(ctx)(purchase),
                      status: TransactionFormatter.statusCheck(ctx)(status),
                      priceAmount: TransactionFormatter.priceAmount(ctx)(services),
                      package: {
                        kind: TransactionFormatter.serviceKind(ctx)(service),
                        value: TransactionFormatter.packageValue(ctx)(services),
                      },
                      discount: TransactionFormatter.discount(ctx)(purchase),
                      totalCash: TransactionFormatter.totalCash(ctx)(payments),
                      avatarUrl: AdvisorProfileFormatter.avatarUrl(ctx)(profile),
                      displayName: AdvisorProfileFormatter.displayName(ctx)(profile),
                    };
                  });

                  const total = _.get(data, '0.purchases_aggregate.aggregate.count', 0);
                  const totalPage = Math.ceil(total / pageCount);
                  return {
                    data: dataSource,
                    total,
                    totalPage,
                    page,
                    pageSize: pageCount,
                  };
                } catch (err) {
                  console.log('err : ', err);
                }
              }, []);

              const pagingModel = React.useMemo(() => {
                // To mt instance vi hm loader l dataLoader.
                const pagingModel = PagingModel.fromDataLoader(dataLoader);
                return pagingModel;
              }, []);

              pagingModel.hookState();

              React.useEffect(() => {
                // Load d liu ln u.
                pagingModel.onLoadPageHandler(0);
              }, []);
              const pagination = {
                items: pagingModel.useState('items'),
                page: pagingModel.useState('page'),
                pageSize: pagingModel.useState('pageSize'),
                total: pagingModel.useState('total'),
                isLoadMore: pagingModel.useState('isLoadMore'),
                isLoading: pagingModel.useState('isLoading'),
                hasPreviousPage: pagingModel.useState('hasPreviousPage'),
                hasNextPage: pagingModel.useState('hasNextPage'),
                onRefresh: () => pagingModel.onRefresh(),
                onPreviousPage: () => pagingModel.onPreviousPageHandler(),
                onNextPage: () => pagingModel.onNextPageHandler(),
                onLoadPage: (page) => pagingModel.onLoadPageHandler(page),
              };
              ctx.apply('REF.setRef', 'transactionPagination', pagination);
              // console.log(pagination);
              return pagination;
            }),
          },
        },
      ],
    ],
  },
  listTransaction: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.get('@page.showDetail')),
        },
      ],
    ],
  },
  detailTransaction: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !!ctx.get('@page.showDetail')),
        },
      ],
    ],
  },

  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
