import { bindings, hook } from '@vl/redata';

const bindData = bindings({
  userHistoryDetail: {
    rules: [
      [
        'data',
        {
          data: {
            '@items': hook((ctx) => {
              const detail = ctx.get('@page.transactionDetail');
              return {
                detail,
              };
            }),
            statusName: hook((ctx) => {
              const statusData = ctx.get('@items.detail.status');
              if (statusData.isConfirmed) {
                return ctx.apply('i18n.t', 'History.Status.completed');
              } if (statusData.isUnconfirmed) {
                return ctx.apply('i18n.t', 'History.Status.pending');
              } if (statusData.isRejected) {
                return ctx.apply('i18n.t', 'History.Status.canceled');
              }
              return ctx.apply('i18n.t', 'History.Status.unknown');
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
