import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import UserPaymentCardItem from '@uz/unitz-components-web/UserPaymentCardItem';
import { Button } from '@uz/unitz-components-web/Button';
import cx from 'classnames';
import _ from 'lodash';

const View8 = () => {
  return (
    <DIV className="userDomesticCardList">
      <section className="py-6 rounded-lg shadow-none min-lg:shadow bg-white500">
        <div className="flex px-4 min-lg:px-6 justify-between items-center">
          <h4 className="text-main text-base font-semibold">{ctx.apply('i18n.t', 'Bank.domesticPayment')}</h4>
          <Button type="primary" size="small">
            {ctx.apply('i18n.t', 'Bank.Action.addCard')}
          </Button>
        </div>
        <div className="border-b mx-4 min-lg:mx-6 py-2 border-divider"></div>
        {!_.isEmpty(ctx.get('cardListModel.listItems')) ? (
          <div>
            {ctx.get('cardListModel.listItems').map((item, index) => {
              return (
                <React.Fragment key={index}>
                  <DIV forceCtx>
                    {ctx.debug(() => {
                      ctx.set('@item', item);
                      ctx.set('@index', index);
                    })}
                    <div
                      className={cx({
                        'bg-brandb-lightest': !!_.get(item, 'is_default'),
                        'bg-transparent': !_.get(item, 'is_default'),
                      })}
                    >
                      <UserPaymentCardItem />
                    </div>
                  </DIV>
                </React.Fragment>
              );
            })}
          </div>
        ) : (
          <div className="flex justify-center pt-14 pb-12 items-center">
            <span className="font-normal text-sm opacity-60 text-sub">{ctx.apply('i18n.t', 'Bank.emptyCardList')}</span>
          </div>
        )}
      </section>
    </DIV>
  );
};

export default View8;
