import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  userHistoryFilter: {
    rules: [
      [
        'data',
        {
          data: {
            '@items': hook((ctx) => {
              const statusData = [
                { label: 'Cha xc nh', value: 'unknown' },
                { label: 'Cuc gi nh', value: 'reject' },
                { label: 'ang x l', value: 'processing' },
                { label: 'Hon thnh', value: 'completed' },
              ];
              const result = {
                statusData,
                onChangeDate: (date) => {
                  const formatDate = {
                    startDate: _.get(date, '0', '').format('YYYY-MM-DD'),
                    endDate: _.get(date, '1', '').format('YYYY-MM-DD'),
                  };
                  ctx.apply('@form.handleChange', 'startDate')(formatDate.startDate);
                  ctx.apply('@form.handleChange', 'endDate')(formatDate.endDate);
                },
                onChangeStatus: (status) => {
                  const newStatus = status || '';
                  ctx.apply('@form.handleChange', 'status')(newStatus);
                },
              };
              return result;
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
