import React from 'react';
import { Input as AntdInput } from 'formik-antd';
import gstyles from '@vl/gstyles';
import styled from 'styled-components';
import _ from 'lodash';

const INPUT_BORDER_RADIUS = '8px';

const normalizeColor = (color) => {
  return {
    darker: color,
    dark: color,
    DEFAULT: color,
    light: color,
    lighter: color,
  };
};

const colors = {
  // Main Color Palette
  primary: gstyles.colors.brandb,
  secondary: gstyles.colors.branda,
  // Accent Color Palette
  red: gstyles.colors.red,
  yellow: gstyles.colors.yellow,
  blue: gstyles.colors.blue,
  // Ink is custom for input style guide
  ink: {
    dark: gstyles.colors.main,
    DEFAULT: gstyles.colors.main,
    light: gstyles.colors.sub,
  },
  // Other
  placeHolder: normalizeColor(gstyles.colors.placeHolder),
  white: normalizeColor(gstyles.colors.white),
  // main: normalizeColor(gstyles.colors.main),
  transparent: normalizeColor('transparent'),
};

const inputState = ({ mainColor, color }) => {
  // states of input.
  return {
    default: {
      color: _.get(color, 'DEFAULT'),
      iconColor: _.get(colors, 'ink.light'),
      borderColor: _.get(mainColor, 'DEFAULT'),
      bgColor: _.get(colors, 'white.DEFAULT'),
      placeHolder: _.get(colors, 'placeHolder.DEFAULT'),
    },
    hover: {
      color: _.get(color, 'light'),
      iconColor: _.get(mainColor, 'light'),
      borderColor: _.get(mainColor, 'light'),
      bgColor: _.get(colors, 'white.DEFAULT'),
      placeHolder: _.get(colors, 'placeHolder.DEFAULT'),
    },
    focus: {
      color: _.get(color, 'light'),
      iconColor: _.get(mainColor, 'light'),
      borderColor: _.get(mainColor, 'light'),
      bgColor: _.get(colors, 'white.DEFAULT'),
      placeHolder: _.get(colors, 'placeHolder.DEFAULT'),
    },
    active: {
      color: _.get(color, 'dark'),
      iconColor: _.get(mainColor, 'dark'),
      borderColor: _.get(mainColor, 'dark'),
      bgColor: _.get(colors, 'white.DEFAULT'),
      placeHolder: _.get(colors, 'placeHolder.DEFAULT'),
    },
    disable: {
      color: _.get(color, 'DEFAULT'),
      iconColor: _.get(colors, 'placeHolder.DEFAULT'),
      borderColor: _.get(colors, 'placeHolder.DEFAULT'),
      bgColor: '#f5f5f5', // antd, this config support for input have icon.
      placeHolder: _.get(colors, 'placeHolder.DEFAULT'),
    },
  };
};

const styledProps = (props) => {
  // overwrite input
  const { name } = props;
  const mainColor = _.get(colors, `${name}`, colors['primary']);
  const color = colors['ink'];
  return inputState({ mainColor, color });
};

const StyledAntdInput = styled(AntdInput)`
  ${(props) => {
    const customInputProps = styledProps(props);
    return `
      &.ant-input,
      &.ant-input-affix-wrapper {
        input::placeholder,
        &::placeholder {
          color: ${_.get(customInputProps, 'default.placeHolder')}
        }
        input::selection
        &::selection{
          background: ${_.get(customInputProps, 'default.borderColor')} !important;
        }
        border-radius: ${INPUT_BORDER_RADIUS};
        color: ${_.get(customInputProps, 'default.color')} !important;
        background: ${_.get(customInputProps, 'default.bgColor')};
        svg {
          filter: ${_.get(gstyles.getFilterCss(_.get(customInputProps, 'default.iconColor')), 'filter')} !important;
        }
        &:hover {
          color: ${_.get(customInputProps, 'hover.color')} !important;
          border-color: ${_.get(customInputProps, 'hover.borderColor')} !important;
          svg {
            filter: ${_.get(gstyles.getFilterCss(_.get(customInputProps, 'hover.iconColor')), 'filter')} !important;
          }
        }
        &:focus {
          color: ${_.get(customInputProps, 'focus.color')} !important;
          border-color: ${_.get(customInputProps, 'focus.borderColor')} !important;
          svg {
            filter: ${_.get(gstyles.getFilterCss(_.get(customInputProps, 'focus.iconColor')), 'filter')} !important;
          }
        }
        &:active {
          color: ${_.get(customInputProps, 'active.color')};
          border-color: ${_.get(customInputProps, 'active.borderColor')} !important;
          svg {
            filter: ${_.get(gstyles.getFilterCss(_.get(customInputProps, 'active.iconColor')), 'filter')} !important;
          }
        }
      }
      &.ant-input-affix-wrapper-focused {
        color: ${_.get(customInputProps, 'focus.color')} !important;
        border-color: ${_.get(customInputProps, 'focus.borderColor')} !important;
        svg {
          filter: ${_.get(gstyles.getFilterCss(_.get(customInputProps, 'focus.iconColor')), 'filter')} !important;
        }
      }
      &.ant-input[disabled],
      &.ant-input-affix-wrapper-disabled {
        input::placeholder{
          color: ${_.get(customInputProps, 'default.placeHolder')}
        }
        color: ${_.get(customInputProps, 'disable.color')} !important;
        border-color: ${_.get(customInputProps, 'disable.borderColor')} !important;
        background: ${_.get(customInputProps, 'disable.bgColor')} !important;
        svg {
          filter: ${_.get(gstyles.getFilterCss(_.get(customInputProps, 'disable.iconColor')), 'filter')} !important;
        }
        &:hover,
        &:focus,
        &:active {
          color: ${_.get(customInputProps, 'disable.color')} !important;
          border-color: ${_.get(customInputProps, 'disable.borderColor')} !important;
          svg {
            filter: ${_.get(gstyles.getFilterCss(_.get(customInputProps, 'disable.iconColor')), 'filter')} !important;
          }
        }
      }
    `;
  }}
`;
// props: type, size, name, icon, block, ghost
export const Input = (props) => {
  const { errorMessage, ...prop } = props;
  return (
    <>
      <StyledAntdInput {...{ ...prop, suffix: _.get(props, 'icon', null) }} />
      {!!errorMessage && (
        <div className="flex items-center mt-1 text-xs text-red">
          {' '}
          {gstyles.icons({
            name: 'help',
            fill: gstyles.colors.red500,
            size: 14,
            className: 'mr-1',
          })}
          {errorMessage}
        </div>
      )}
    </>
  );
};

export default Input;
