import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';
import gstyles from '@vl/gstyles';
import cx from 'classnames';

const bindData = bindings({
  UserHistoryList: {
    rules: [
      [
        'data',
        {
          data: {
            '@items': hook((ctx) => {
              // status: reject, early_end, completed, on_going, pending.
              const pagination = ctx.get('@pagination');
              // console.log(pagination);
              const columns = [
                {
                  title: (
                    <div className="text-sub font-semibold text-sm">
                      {ctx.apply('i18n.t', 'History.Table.createdAt')}
                    </div>
                  ),
                  dataIndex: 'createdAt',
                  key: 'createdAt',
                  width: 200,
                },
                {
                  title: (
                    <div className="text-sub font-semibold text-sm">
                      {ctx.apply('i18n.t', 'History.Table.displayName')}
                    </div>
                  ),
                  dataIndex: 'displayName',
                  key: 'displayName',
                },
                {
                  title: (
                    <div className="text-sub font-semibold text-sm">{ctx.apply('i18n.t', 'History.Table.during')}</div>
                  ),
                  dataIndex: 'timeCall',
                  key: 'timeCall',
                },
                {
                  title: (
                    <div className="text-sub font-semibold text-sm">{ctx.apply('i18n.t', 'History.Table.type')}</div>
                  ),
                  dataIndex: 'package',
                  key: 'package',
                  render: (item) => (
                    <div className="flex justify-start items-center">
                      <div className="table-cell align-top">
                        {gstyles.icons({
                          name: `${_.get(item, 'kind', '')}-call`,
                          size: 24,
                          fill: gstyles.colors.brandb500,
                        })}
                      </div>
                    </div>
                  ),
                },
                {
                  title: (
                    <div className="text-sub font-semibold text-sm">{ctx.apply('i18n.t', 'History.Table.status')}</div>
                  ),
                  dataIndex: 'status',
                  key: 'status',
                  width: 150,
                  render: (item) => {
                    const statusName = () => {
                      if (item.isConfirmed) {
                        return ctx.apply('i18n.t', 'History.Status.completed');
                      } if (item.isUnconfirmed) {
                        return ctx.apply('i18n.t', 'History.Status.pending');
                      } if (item.isRejected) {
                        return ctx.apply('i18n.t', 'History.Status.canceled');
                      }
                      return ctx.apply('i18n.t', 'History.Status.unknown');
                    };
                    return (
                      <div
                        className={cx('text-center font-semibold rounded-md px-1 w-auto', {
                          'bg-brandb-lighter text-brandb500': item.isConfirmed,
                          'bg-yellow-lightest text-yellow500': item.isUnconfirmed,
                          'bg-red-lightest text-red500': item.isRejected,
                        })}
                      >
                        {statusName()}
                      </div>
                    );
                  },
                },
                {
                  title: (
                    <div className="text-right text-sub font-semibold text-sm">
                      {ctx.apply('i18n.t', 'History.Table.priceAmount')}
                    </div>
                  ),
                  dataIndex: 'priceAmount',
                  key: 'priceAmount',
                  render: (item) => <div className="text-right">{item}</div>,
                },
                {
                  dataIndex: 'more',
                  key: 'more',
                  render: () => gstyles.icons({ name: 'more', fill: gstyles.colors.sub }),
                },
              ];
              return {
                pagination: {
                  current: pagination.page + 1,
                  pageSize: pagination.pageSize,
                  total: pagination.total,
                  onChange: (page) => {
                    pagination.onLoadPage(page - 1);
                  },
                },
                dataSource: pagination.items,
                columns,
                onClickRow: (record) => {
                  // TODO
                  ctx.apply('@page.onClickRow', record);
                },
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
