import { bindings, hook } from '@vl/redata';
import UserWalletModelFormater from '@uz/unitz-models/UserWalletModel/formatter';
// import useRoute from '@vl/hooks/useGbRoute';

const bindData = bindings({
  advisorMenus: {
    rules: [
      [
        'data',
        {
          data: {
            '@item': hook((ctx) => {
              const wallets = ctx.get('wallets', []);
              return {
                profile: {
                  displayName: ctx.get('userInfo.displayName', ''),
                  avatarUrl: ctx.get('userInfo.avatarUrl', ''),
                },
                amountBalance: UserWalletModelFormater.amountBalance(ctx)(wallets),
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
