import { BaseModel } from '@uz/unitz-models/BaseModel';
import _ from 'lodash';
// eslint-disable-next-line import/prefer-default-export
export default class PagingModel extends BaseModel {
  state = {
    items: [],
    page: 0,
    isLoading: false,
    isLoadMore: false,
    hasPreviousPage: false,
    hasNextPage: true,
    hasLoadMore: true,
    pageSize: 0,
    total: 0
  };

  constructor(...args) {
    super(...args);
    this.constructor.initState(this);
    this.pageSize = 0;
    this.totalPage = 0;
    this.dataLoader = null;
  }

  static fromDataLoader(dataLoader) {
    // Hm  load thm d liu t bn ngoi. Gi query bn ngoi.
    try {
      const instance = new PagingModel();
      instance.dataLoader = dataLoader;
      return instance;
    } catch (err) {
      return null;
    }
  }

  async onRefresh() {
    try {
      this.setState({
        isLoading: true,
        hasNextPage: true,
        hasPreviousPage: false,
        isLoadMore: false
      });

      const res = await this.dataLoader({ page: 0 });

      this.pageSize = res.total;
      this.totalPage = res.totalPage;
      this.setState({
        items: res.data,
        page: 0
      });
    } catch (error) {
      console.log('error', error);
    } finally {
      this.setState({
        isLoading: false
      });
    }
  }

  async onPreviousPage() {
    try {
      this.setState({
        isLoadMore: true
      });
      this.useState('items');
      const page = this.useState('page');

      if (page === 0) return;

      const newPage = page - 1;

      const res = await this.dataLoader({ page: newPage });

      this.setState({
        items: [...res.data],
        page: newPage,
        hasPreviousPage: newPage > 0,
        hasNextPage: newPage < this.totalPage
      });
    } catch (error) {
      console.log('error', error);
    } finally {
      this.setState({
        isLoadMore: false
      });
    }
  }

  async onNextPage() {
    //   onEndReached => onNextPage
    try {
      this.setState({
        isLoadMore: true
      });

      this.useState('items');
      const page = this.useState('page');
      const newPage = page + 1;

      if (newPage > this.totalPage) return;

      const res = await this.dataLoader({ page: newPage });

      this.setState({
        items: [...res.data],
        page: newPage,
        hasPreviousPage: newPage > 0,
        hasNextPage: newPage < this.totalPage
      });
    } catch (error) {
      console.log('error', error);
    } finally {
      this.setState({
        isLoadMore: false
      });
    }
  }

  async onLoadMore() {
    try {
      this.setState({
        isLoadMore: true
      });

      const items = this.useState('items');
      const page = this.useState('page');
      const newPage = page + 1;
      const res = await this.dataLoader({ page: newPage });
      this.totalPage = res.totalPage;
      const pageSize = _.get(res.data, 'length');
      const _items = _.uniqBy([...items, ...res.data], (item) => _.get(item, 'id'));
      const _res = await this.dataLoader({ page: newPage + 1 });

      this.setState({
        items: _items,
        page: newPage,
        hasPreviousPage: newPage > 0,
        hasNextPage: pageSize > 0,
        hasLoadMore: !!_.get(_res.data, 'length')
      });
    } catch (error) {
      console.log('error', error);
    } finally {
      this.setState({
        isLoadMore: false
      });
    }
  }

  async onLoadPage(newPage) {
    try {
      this.setState({
        isLoadMore: true
      });

      this.useState('items');
      this.useState('page');

      if (newPage > this.totalPage || newPage < 0) return;

      const res = await this.dataLoader({ page: newPage });
      this.totalPage = res.totalPage;
      // this.total = res.total;
      this.setState({
        items: [...res.data],
        page: newPage,
        hasPreviousPage: newPage > 0,
        hasNextPage: newPage < this.totalPage,
        pageSize: res.pageSize,
        total: res.total
      });
    } catch (error) {
      console.log('error', error);
    } finally {
      this.setState({
        isLoadMore: false
      });
    }
  }

  onPreviousPageHandler() {
    const isLoading = this.useState('isLoading');
    const isLoadMore = this.useState('isLoadMore');
    if (!isLoading && !isLoadMore) {
      this.onPreviousPage();
    }
  }

  onNextPageHandler() {
    const hasNextPage = this.useState('hasNextPage');
    const isLoading = this.useState('isLoading');
    const isLoadMore = this.useState('isLoadMore');
    if (hasNextPage && !isLoading && !isLoadMore) {
      this.onNextPage();
    }
  }

  onLoadPageHandler(newPage) {
    this.useState('hasNextPage');
    const isLoading = this.useState('isLoading');
    const isLoadMore = this.useState('isLoadMore');
    if (!isLoading && !isLoadMore) {
      this.onLoadPage(newPage);
    }
  }

  extends(config) {
    Object.assign(this, config);
    return this;
  }
}
