import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

import UserModel from '@uz/unitz-models/UserModel';
import AdvisorModelFormatter from '@uz/unitz-models/AdvisorModel/formatter';

import NotificationModelFormatter from '@uz/unitz-models/NotificationModel/formatter';
import NotificationModelRenderer from '@uz/unitz-models/NotificationModel/renderer';
import UserModelFormatter from '@uz/unitz-models/UserModel/formatter';

import usePromiseSource from '@vl/hooks/usePromiseSource';
import NotificationModel from '@uz/unitz-models/NotificationModel';
import AdvisorModel from '@uz/unitz-models/AdvisorModel';

const bindData = bindings({
  component: {
    rules: [
      [
        'data',
        {
          data: {
            '@item': hook((ctx) => {
              const itemData = ctx.get('@item');
              const advisorId = _.get(itemData, 'payload.session.advisor_id', '');
              const service_id = _.get(itemData, 'payload.session.service_id', '');
              const user_id = _.get(itemData, 'payload.session.user_id', '');

              const userInfo = usePromiseSource(
                async () => {
                  try {
                    const items = await UserModel.find(
                      `where: {id: {_eq: "${user_id}"}}`,
                      `profile {
                        avatar_url
                        display_name
                        id
                      }
                    `
                    );

                    return items.toObject();
                  } catch (err) {
                    console.log(err);
                  }
                },
                null,
                [user_id]
              );

              const advisorInfo = usePromiseSource(
                async () => {
                  try {
                    const items = await AdvisorModel.find(
                      `where: {id: {_eq: "${advisorId}"}}`,
                      `profile {
                        avatar_url
                        display_name
                        ref_ctf_eid
                      }
                      services(where: {id: {_eq: "${service_id}"}}) {
                        service {
                          kind
                          name
                        }
                        price {
                          per_amount
                          per_unit
                          price_amount
                          price_currency
                        }
                      }
                    `
                    );

                    return items.toObject();
                  } catch (err) {
                    console.log(err);
                  }
                },
                null,
                [service_id, advisorId]
              );

              const service = _.get(advisorInfo, '0.services.0', {});
              const [seen, $seen] = React.useState(false);

              React.useEffect(() => {
                $seen(_.get(itemData, 'seen'));
              }, [_.get(itemData, 'seen')]);

              const userItem = _.get(userInfo, '0');

              const onSeenNotification = React.useCallback(async (item) => {
                try {
                  if (seen) return;

                  $seen(true);
                  const id = _.get(item, 'notificationId', '');
                  const result = await NotificationModel.seenNotification({ id });
                  if (!result) {
                    $seen(false);
                  }
                } catch (error) {
                  console.log(error);
                }
              }, []);
              const content = NotificationModelFormatter.message(ctx)(itemData, {
                packageName: { value: AdvisorModelFormatter.packageName(ctx)(service) },
                userLink: { value: UserModelFormatter.displayName(ctx)(userItem), isAction: true },
              });
              return {
                ...itemData,
                notificationId: _.get(itemData, 'id'),
                seen,
                content: NotificationModelRenderer.renderHTML(ctx)(content),
                profile: {
                  displayName: UserModelFormatter.displayName(ctx)(userItem),
                  avatarUrl: UserModelFormatter.avatarUrl(ctx)(userItem),
                },
                time: NotificationModelFormatter.timeNow(ctx)(itemData),
                icon: _.get(service, 'service.kind', ''),
                onSeenNotification,
              };
            }),
          },
        },
      ],
    ],
  },
});
export default bindData;
