import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  userTransactionDetail: {
    rules: [
      [
        'data',
        {
          data: {
            '@items': hook((ctx) => {
              const detail = ctx.get('@page.transactionDetail');
              const id = _.last(_.split(_.get(detail, 'key', ''), '-'));
              return {
                id,
                detail,
              };
            }),
            statusName: hook((ctx) => {
              const statusData = ctx.get('@items.detail.status');
              if (statusData.isConfirmed) {
                return ctx.apply('i18n.t', 'Transaction.Status.completed');
              }
              if (statusData.isUnconfirmed) {
                return ctx.apply('i18n.t', 'Transaction.Status.pending');
              }
              if (statusData.isCanceled) {
                return ctx.apply('i18n.t', 'Transaction.Status.canceled');
              }
              return ctx.apply('i18n.t', 'Transaction.Status.unknown');
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
