import { bindings, hook } from '@vl/redata';
import React from 'react';
import _ from 'lodash';
import CardWalletModel from '@uz/unitz-models/CardWalletModel';

const bindData = bindings({
  userDomesticCardList: {
    rules: [
      [
        'data',
        {
          data: {
            '@items': hook((ctx) => {
              const items = ctx.get('cardList');
              return items || [];
            }),

            cardListModel: hook((ctx) => {
              const cardList = ctx.get('cardList', []);

              const listItems = React.useMemo(() => {
                return _.filter(cardList, (item) => !['visa', 'mastercard'].includes(_.lowerCase(_.get(item, 'bank_code'))));
              }, [JSON.stringify(cardList)]);
              const [activeId, $activeId] = React.useState(null);

              const getId = (item) => _.get(item, 'id');
              const [items, $items] = React.useState([]);

              React.useEffect(() => {
                $items(listItems);
              }, [JSON.stringify(cardList), activeId]);

              return {
                listItems: items,
                isActive: (item) => _.get(item, 'id') === activeId,
                activeId,
                toggleItem: async (item) => {
                  const id = _.get(item, 'id');
                  try {
                    const card = await CardWalletModel.fromId({ id });
                    if (card) {
                      card.is_default = true;
                      const res = await card.save();
                      if (res && _.get(res, 'id')) {
                        $activeId(_.get(res, 'id'));
                      }
                    }
                  } catch (err) {
                    console.log('err', err);
                  }
                },
                remove: async (item) => {
                  const id = getId(item);
                  $activeId(id);
                  try {
                    const card = await CardWalletModel.fromId({ id });
                    card && (await card.delete());
                  } catch (err) {
                    console.log('err', err);
                  }
                },
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
