import React from 'react';

import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import { resable } from '@vl/redata/RESABLE';
import RESABLE from '@vl/redata/RESABLE.macro';
import { UnAuthRedirect } from '@uz/unitz-components-web/AppRedirect';
import UserWalletSurplus from '@uz/unitz-components-web/UserWalletSurplus';
import _ from 'lodash';

const Index = () => {
  // @TODO: validate missing required input
  return (
    <DIV className="pageContainer">
      <UnAuthRedirect to={ctx.apply('authModel.generateLoginRedirectUrl')}>
        <h1 className="py-4 text-main text-xl font-semibold text-center min-lg:text-left m-0 text-xl leading-12">
          {ctx.apply('i18n.t', 'Wallet.walletTxt')}
        </h1>
        <RESABLE>
          <DIV>
            {ctx.debug(() => {
              const wallets = resable.for('wallets')(ctx.get('wallets'), []);
              ctx.set('wallets', _.get(wallets, '0.wallets', []));
            })}
            <UserWalletSurplus />
          </DIV>
        </RESABLE>
      </UnAuthRedirect>
    </DIV>
  );
};

export default Index;
