import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';
import { useRenderProps } from '@vl/hooks/useRenderProps';
import { renderPropsComposer } from '@vl/mod-utils/PropsComposer';
import UserCompletedCall from '@uz/unitz-components-web/UserCompletedCall';
import UserMissedCall from '@uz/unitz-components-web/UserMissedCall';
import FlatList from '@uz/unitz-components-web/FlatList';
import { Row, Col } from 'antd';
import cx from 'classnames';

const renderItem = useRenderProps(
  ({ item, index }) => {
    return (
      <Col
        span={24}
        className={cx({
          'bg-brandb-lightest cursor-pointer': !_.get(item, 'seen', ''),
          'bg-white500': !!_.get(item, 'seen', ''),
        })}
      >
        <DIV forceCtx>
          {ctx.debug(() => {
            ctx.set('@item', item);
            ctx.set('@index', index);
          })}
          {renderPropsComposer(
            {
              matcher: (props) => _.get(props, 'type_id') === 'completed_call_user',
              render: () => <UserCompletedCall />,
            },
            { matcher: (props) => _.get(props, 'type_id') === 'missed_call_user', render: () => <UserMissedCall /> },
            () => null
          )(item)}
        </DIV>
      </Col>
    );
  },
  ({ item, index }) => [index, _.get(item, 'id')]
);

const View8 = () => (
  <DIV className="notificationListX">
    <div>
      <FlatList
        as={Row}
        data={ctx.get('@items', [])}
        renderItem={renderItem}
        keyExtractor={(item, index) => `${_.get(item, 'id')}_${index}`}
      />
    </div>
  </DIV>
);

export default View8;
