const Index = () => {
  // @TODO: validate missing required input
  return (
    <DIV className="pageContainer">
      <UnAuthRedirect to={ctx.apply('authModel.generateLoginRedirectUrl')}>
        <h1 className="py-4 text-main text-xl font-semibold text-center min-lg:text-left m-0 text-xl leading-12">
          {ctx.apply('i18n.t', 'Bank.bankListTxt')}
        </h1>
        <div className="px-0 mb-6">
          <RESABLE>
            <DIV>
              {ctx.debug(() => {
                const dataCardList = resable.for('cardList')(ctx.get('cardList'), []);
                ctx.set('cardList', dataCardList);
              })}
              <UserInternationalCardList />
            </DIV>
          </RESABLE>
        </div>
        <div className="px-0">
          <UserDomesticCardList />
        </div>
      </UnAuthRedirect>
    </DIV>
  );
};

export default Index;
