import { bindings, hook } from '@vl/redata';
import UserModel from '@uz/unitz-models/UserModel';
import { FULL_NAME, PHONE_NUMBER } from '@vl/mod-utils/validateUtil';
import { useFormik } from 'formik';
import * as Yup from 'yup';

const bindData = bindings({
  pageContainer: {
    rules: [
      [
        'data',
        {
          data: {
            '@item': hook((ctx) => {
              return {
                documents: [
                  {
                    value: 'cccd',
                    label: ctx.apply('i18n.t', 'ProfileAdvisor.identify'),
                  },
                  {
                    value: 'passport',
                    label: ctx.apply('i18n.t', 'ProfileAdvisor.passport'),
                  },
                ],
              };
            }),

            form: hook((ctx) => {
              const displayName = ctx.get('userInfo.displayName', '');
              const phoneNumber = ctx.get('userInfo.phoneNumber', '');
              const userId = ctx.apply('authModel.getUserId');
              const form = useFormik({
                enableReinitialize: true,
                initialValues: {
                  name: displayName,
                  birthday: '',
                  phone: phoneNumber,
                  email: ctx.get('authModel.currentUser.email'),
                  taxNumber: '',
                  typeOfIdentity: '',
                  identifierNumber: '',
                },
                onSubmit: async (values) => {
                  try {
                    const result = await UserModel.updateProfile({
                      user_id: userId,
                      display_name: values.name,
                      phone_number: values.phone,
                    });
                    if (result) {
                      // eslint-disable-next-line
                      alert && alert(ctx.apply('i18n.t', 'ProfileUser.updateSuccess'));
                    }
                  } catch (error) {
                    console.log({ error });
                  }
                },
                validationSchema: Yup.object().shape({
                  name: Yup.string()
                    .required(ctx.apply('i18n.t', 'ProfileUser.required'))
                    .matches(FULL_NAME, ctx.apply('i18n.t', 'Validation.fullName')),
                  phone: Yup.string()
                    .required(ctx.apply('i18n.t', 'ProfileUser.required'))
                    .matches(PHONE_NUMBER, ctx.apply('i18n.t', 'Validation.phoneNumber')),
                }),
              });
              // console.log({ form });
              return form;
            }),
            canSubmitInfo: hook((ctx) => {
              return ctx.get('form.isValid') && !ctx.get('form.isSubmitting') && ctx.get('form.dirty');
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
